import React from 'react';
import {Link} from "react-router-dom";

function Sidebar() {
    return (
        <div id="layoutSidenav_nav">
            {/* <!-- Sidebar --> */}
            <nav className="sb-sidenav accordion sb-sidenav-dark" id="sidenavAccordion">
                <div className="sb-sidenav-menu">
                    <div className="nav">
                        <div className="sb-sidenav-menu-heading">Core</div>
                        <a className="nav-link small" href="#">
                            <Link to="/biogas/">
                                <div className="sb-nav-link-icon">
                                    <i className="fas fa-fw fa-tachometer-alt"></i>
                                    <span className="text-light font-weight-bold mx-2">Dashboard</span>
                                </div>
                            </Link>
                        </a>
                        <a className="nav-link small" href="#">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-question-circle"></i>
                                <span className="text-light font-weight-bold mx-2">About</span>
                            </div>
                        </a>
                        
                        <div className="sb-sidenav-menu-heading">Sections</div>
                        <a className="nav-link small" href="#measured-data">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-database"></i>
                                <span className="text-light font-weight-bold mx-2">Measured Data</span>
                            </div>
                        </a>
                        <a className="nav-link small" href="#data-charts">
                            <div className="sb-nav-link-icon">
                                <i className="fas fa-fw fa-chart-bar"></i>
                                <span className="text-light font-weight-bold mx-2">Charts</span>
                            </div>
                        </a>
                    </div>
                </div>
            </nav>
            {/* <!-- End of Sidebar --> */}
        </div>
    )
};

export default Sidebar;