import axios from 'axios';
import timestamp from 'unix-timestamp';

timestamp.round = true;
const requestContext = {
    reactor: [
        'temperature_a',
        'temperature_b',
        'pressure',
        'flow'
    ],
    pressurized: [
        'temperature',
        'pressure',
        'level',
        'flow'
    ],
    normal: [
        'temperature',
        'pressure',
        'flow'
    ]
};

function random() {
    let [x, y] = [0, 0];
    while(x === 0) x = Math.random();
    while(y === 0) y = Math.random();
    let number = Math.sqrt( -2.* Math.log( x ) ) * Math.cos( 2.* Math.PI * y );
    number = (number / 10.) + .5;
    if (number > 1 || number < 0) return random();
    return number;
}

export function sendDataToAPI() {
    Object.keys (requestContext).forEach (
        (key) => {
            const payload = {'unix': timestamp.now()};
            requestContext[key].forEach (f => payload[f] = Math.round (
                random() * 1e+04) / 1e+02
            );

            axios
                .post (`http://sancaliar82.pythonanywhere.com/biogas/${key}/`, payload)
                .catch (error => console.log(error))
        }
    );
};