/* Main.Js */
import timestamp from 'unix-timestamp';
import React, { Component } from 'react';
import { Switch } from 'react-router-dom';

import { sendDataToAPI } from './assets/js/Request';
import { ApiContext } from './assets/js/contexts/ApiContext';
import { Private } from './assets/js/utils/AccessRoute';

import 'bootstrap';
import 'startbootstrap-sb-admin-2/css/sb-admin-2.min.css';
import 'startbootstrap-sb-admin-2/vendor/fontawesome-free/css/all.min.css';

import './assets/css/navigation.css';
import './assets/css/diagram.css';
import './assets/css/custom.css';

import Sidebar from './components/sidebar';
import Topbar from './components/topbar';
import Footer from './components/footer';

import Dashboard from './views/dashboard';
import Error from './views/error';

class Main extends Component {
    static contextType = ApiContext;

    constructor(props) {
        super(props);
        this.state = {
            toggle: true,
            isUpdated: false
        };
        
        this.toggle = this.toggle.bind(this);
        timestamp.round = true;
    };

    toggle(event) {
        event.preventDefault();
        this.setState(
            {toggle: !this.state.toggle}
        );
    };

    componentWillMount() {
        this.setState(
            {isUpdated: this.context.getRequest()}
        );
    };

    componentDidMount() {
        let [sendPeriode, getPeriode] = [30, 10];
        let [tsend, tget] = [
            timestamp.now(),
            timestamp.now(),
        ];

        this.interval = setInterval(
            () => {
                let tnow = timestamp.now();
                if (tnow > tsend) {sendDataToAPI(); tsend = tsend + sendPeriode}
                else if (tnow > tget) {
                    tget = tget + getPeriode;
                    this.setState(
                        {isUpdated: this.context.getRequest()}
                    );
                }
            }, 1000
        );
    };

    componentWillUnmount() {
        clearInterval(this.interval);
    };

    render() {
        return (
            <div id="content-wrapper" className="d-flex flex-column">
                <div id="content" className={`sb-nav-fixed ${this.state.toggle ? null : "sb-sidenav-toggled"}`}>
                    <Topbar func={this.toggle} />
                    
                    <div id="layoutSidenav">
                        <Sidebar/>
                        <div id="layoutSidenav_content">
                            <Switch>
                                <Private exact path="/biogas/" component={Dashboard} />
                                <Private exact path="/biogas/help/" component={Error} />
                            </Switch>
                            <Footer />
                        </div>
                    </div>
                    
                </div> 
            </div>
        );
    };
};

export default Main;
