import React, { Component } from 'react';
import Chart from 'react-apexcharts';

class ApexChartLine extends Component {
    constructor(props) {
        super(props);
        this.state = {
            series: [{
                name: this.props.chartLabel,
                data: []
            }],
            options: {
                chart: {
                    group: this.props.group,
                    id: 'apexchart',
                    type: 'line',
                    height: 150,    
                    animations: {
                        enabled: true,
                        easing: 'easein',
                        dynamicAnimation: {
                            speed: 250
                        }
                    },
                    toolbar: {
                        show: false
                    }
                },
                fill: {
                    type: 'gradient',
                    gradient: {
                        shadeIntensity: 1,
                        opacityFrom: 0.75,
                        opacityTo: 0.95,
                        stops: [0, 100]
                    }
                },
                stroke: {
                    curve: 'straight',
                    lineCap: 'round',
                    width: 3
                },
                xaxis: {
                    type: 'category',
                    labels: {
                        show: false,
                        style: {
                            colors: [],
                            fontFamily: font.family.custom,
                            fontSize: '12px'
                        }
                    },
                    axisTicks: {
                        show: true,
                        borderType: 'solid',
                        color: '#78909C',
                        height: 4
                    },
                    tooltip: {
                        enabled: false
                    }
                },
                yaxis: {
                    tickAmount: (3),
                    labels: {
                        show: true,
                        style: {
                            fontFamily: font.family.custom,
                            fontSize: '12px'
                        }
                    },
                    title: {
                        offsetY: 8,
                        text: this.props.chartLabel,
                        style: {
                            fontFamily: font.family.custom,
                            fontWeight: 'normal',
                            fontSize: '13px'
                        }
                    }
                },
                tooltip: {
                    enabled: true,
                    fillSeriesColor: false,
                    style: {
                        fontFamily: font.family.custom,
                        fontSize: '12px'
                    }
                },
                dataLabels: {
                    enabled: false
                },
                grid: {
                    row: {
                        colors: ['#f3f3f3', 'transparent'],
                        opacity: 0.5
                    },
                }
            }
        };
    };
    render() {

        const series = [{
            name: this.props.chartLabel,
            data: this.props.series
        }];

        return (
            <div className="d-flex flex-column mb-0">
                <Chart options={this.state.options} series={series} type="line"
                    height={this.props.chartHeight} />
            </div>
        );
    };
};

const font = {
    size: {
        name: '12px',
        value: '32px'
    },
    family: {
        default: 'Nunito, sans-serif',
        custom: 'Titillium Web, sans-serif'
    }
};
export default ApexChartLine;